	import Validator from '../../common/validator.esm.js';
	const validator = new Validator();
export default {
	data() {
		return {
			sumstring: [],
			postData: [],
			flag: true,
			check: "check",
			ischeck: false,
			code: "",
			selectname: [],
			// counttype: "请选择服务类型",
			name: "",
			worktime: "",
			phonenum: "获取手机号码",
			list: "list",
			rSelect: [],
			typefy: "",
			typename: [],
			height: "",
			background: {
				backgroundImage: "linear-gradient(90deg, #00ABEB, #54C3F1)",
			},
		}
	},
	computed: {
		counttype() {
			let selectname = this.selectname;
			this.sumstring = this.selectname;
			return this.typefy + "," + this.sumstring
		}
	},
	onLoad(option) {
		let that = this;
		console.log(option.id); //打印出上个页面传递的参数。
		let latitude = uni.getStorageSync("latitude");
		console.log(latitude);
		this.typefy = option.id;
		let typedata = uni.getStorageSync("typedata");
		for (var typename in typedata) { //遍历对象属性名
			if (option.id == typename) {
				// console.log(typeof(typedata));
				// console.log(typedata[typename]);
				this.typename = typedata[typename];
				console.log(this.typename);
			}
		};
		uni.login({
			provider: 'weixin',
			success(res) {
				console.log(res.code);
				that.code = res.code
			}
		})
	},
	methods: {
		//选择二级分类服务类型
		selecttype(e) {
			if (this.rSelect.indexOf(e) == -1) {
				console.log(e) //打印下标
				this.rSelect.push(e); //选中添加到数组里
				this.selectname.push(this.typename[e]);
			} else {
				this.rSelect.splice(this.rSelect.indexOf(e), 1); //取消
				this.selectname.splice(this.typename.indexOf(e), 1);
			}
		},
		//获取手机号码
		getPhoneNumber(e) {
			console.log(e.detail.errMsg);
			console.log(e.detail.iv);
			console.log(e.detail.encryptedData);
			let iv = e.detail.iv;
			let encryptedData = e.detail.encryptedData;
			let code = this.code;
			let that = this;
			//检查session——key是否过期
			uni.checkSession({
				success(res) {
					console.log(res);
					uni.request({
						url: "https://applet.51tiaoyin.com/public/applet/user/get_phone",
						method: 'GET',
						dataType: JSON,
						data: {
							"code": code,
							"iv": iv,
							"encryptedData": encryptedData
						},
						success(res) {
							console.log(res.data);
							let data = JSON.parse(res.data);
							console.log(data);
							if (data.code == 200) {
								// console.log(this);
								console.log(data.data);
								that.phonenum = data.data;
							} else {
								uni.showToast({
									title: "没有获取手机号",
									duration: 1500
								})
							}
						},
						fail(res) {
							console.log(res)
						}
					})
				},
				fail(err) {
					// session_key 已经失效，需要重新执行登录流程
					uni.login({
						success: res => {
							that.data.code = res.code
						}
					})
				}
			})

			// 	//-----------------是否授权，授权通过进入主页面，授权拒绝则停留在登陆界面
			if (e.detail.errMsg == 'getPhoneNumber:user deny') { //用户点击拒绝
				uni.showToast({
					title: "拒绝获取手机号码"
				})

			} else { //允许授权执行跳转
				uni.showToast({
					title: "正在获取手机号码"
				})

			}
		},

		//选中协议
		checked() {
			return this.flag = !this.flag
		},
		//表单提交
		submit() {
			let name = true,
				phone = true,
				worktime = true,
				counttype = true;
			validator
				.init()
				.add(this.name, [{
						type: 'required',
						message: '用户名不能为空'
					},
					{
						type: 'min:2',
						message: '用户名长度不能小于2位!'
					},
					{
						type: 'max:6',
						message: '用户名长度不能大于6位!'
					}
				])
				.add(this.worktime, [{
						type: 'required',
						message: '手机号码不能为空'
					},
					{
						type: 'max:2',
						message: '工龄必须输入数字',
						callback :(value, message) =>  {
							var reg = '\d{2}';
						      return reg.test(value) ? void 0 : message 
						    }
					}
				])
				.add(this.phone, [{
						type: 'required',
						message: '手机号码不能为空'
					},
					{
						type: 'phone',
						message: '手机号码格式不正确'
					}
				])
				.add(this.counttype, [{
						type: 'required',
						message: '服务类型不能为空'
					},
				])
			const errorMsg = validator.validation()
			if (errorMsg) {
				uni.showToast({
					title:errorMsg
				});
				// console.error(errorMsg)
			} else {
				uni.showToast({
					title:"提交中"
				});
				let latitude = uni.getStorageSync("latitude");
				let longitude = uni.getStorageSync("longitude")
				uni.request({
					url: "https://applet.51tiaoyin.com/public/applet/user/get_info",
					method: "POST",
					data: {
						name: this.name,
						age: this.worktime,
						phone: this.phonenum,
						coord: latitude + "," + longitude,
						type: this.counttype
					},
					success(res) {
						console.log(res);
					},
					fail() {
						console.log("失败：" + res);
					}
				})
			}
			// //姓名校验
			// if(this.name == ""){
			// 	uni.showToast({
			// 		title:"姓名不能为空"
			// 	});
			// 	name = false;
			// }else{

			// }
			// //手机号码校验
			// if(this.phonenum == ""){
			// 	uni.showToast({
			// 		title:"手机号不能为空"
			// 	})
			// }
			// //工作时间校验
			// if(this.worktime == ""){
			// 	uni.showToast({
			// 		title:"工作时间不能为空"
			// 	})
			// }else{

			// }
			// if(this.counttype == ""){
			// 	uni.showToast({
			// 		title:"请选择下方服务类型"
			// 	})
			// }
			
		}
	},
	onReady() {

	}
}
